import {Injectable} from "@angular/core";

@Injectable()
export class NotesServices  {
	notes = [
		{id:1 , title: "titulo 1", description: "Descripcion 1"},
		{id:2 , title: "titulo 2", description: "Descripcion 2"},
		{id:3 , title: "titulo 3", description: "Descripcion 3"}
	]

	public getNotes () {
		return this.notes;
	}
}


